  z-index: 3;
  width: 100vw;
  height: 100%;
  opacity: 1;
  visibility: visible;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.tmp-backdrop--is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.tmp-backdrop--is-hidden .tmp-backdrop__modal {
  transform: translate(-50%, -50%)scale(.8);
}

.tmp-backdrop__modal {
  z-index: 4;
  background-color: #fff;
  border-radius: 4px;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
}

.tmp-backdrop__modal-close {
  cursor: pointer;
  width: 30px;
  height: 30px;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 8px;
  right: 8px;
}

